// import sunuiStar from '../../../../components/sunui-star/sunui-star.vue';
export default {
	data() {
		return {
			downOption: {
				auto: false //是否在初始化后,自动执行下拉回调callback; 默认true
			},
			upOption: {
				auto: false
			},
			detail_list:[
				{
					industry:'铝电行业',
					equipment:'全自动热压机',
					type:'电工',
					task_cycle:'20天',
					work_date:'2019-09-21 ~ 2019-10-21'
				},
				{
					industry:'铝电行业',
					equipment:'全自动热压机',
					type:'电工',
					task_cycle:'20天',
					work_date:'2019-09-21 ~ 2019-10-21'
				},
			],
			id:'',
			pid:'',
			detail_data:'',
			isWork:false,
		};
	},
	components: {
		// sunuiStar
	},
	computed:{
		c_CustomBar(){
			return this.CustomBar
		},
		personinfo(){
			return this.$utils._get(this.detail_data,'personinfo',{})
		},
		files(){
			return this.$utils._get(this.detail_data,'files',[])
		},
		works(){
			return this.$utils._get(this.detail_data,'works',[])
		}
	},
	onLoad(option) {
		this.id = option.id
		this.pid = option.pid
		this.isWork = this.$_.get(option,'isWork',false)
		uni.$on('refreshList',() =>{
			console.log('refreshList')
			this.init()
		})
		this.init()
	},
	onUnload() {
		uni.$off('refreshJwt')
	},
	methods: {
		init(){
			this.$http.get('personwx.persondetail/1.0/',{
				id:this.pid
			}).then(res =>{
				console.log(res)
				if(res.data.code == 0){
					this.detail_data = res.data.data
				}
				
			})
		},
		downCallback(mescroll) {
			// 这里加载你想下拉刷新的数据, 比如刷新轮播数据
			// loadSwiper();
			// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
			mescroll.resetUpScroll();
		},
		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
		upCallback(mescroll) {
			//联网加载数据
			mescroll.endErr();
			// setTimeout(() => {
			// 	mescroll.endErr();
			// }, 1000);
		},
		go_back(status){
			let pamars = {
				status:status,
				id:this.id
			}
			this.$http.post('personwx.checkrecruitperson/1.0/',pamars).then(res =>{
				console.log(res)
				if(res.data.code == '0'){
					uni.$emit('refreshzmList')
					uni.navigateBack({
					    delta: 1
					});
				}
				
			})
			
		},
		get_work_time(item){
			return this.$utils.format_date(item.sstartTime,'/') +' - '+this.$utils.format_date(item.sendTime,'/')
		},
		avatarUrl(avatarUrl){
			return `url(${avatarUrl})`
		},
	}
};
